import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../styles/main.css';
import naver from '../../image/naver.png';
import kakao from '../../image/kakao.png';
import logo from '../../image/logo.png';

// Login  
function Login() {
  // formData  (state) 
  const [formData, setFormData] = useState({
    id: '',
    password: ''
  });

  //   
  const [loginSuccess, setLoginSuccess] = useState(true);

  //     navigate  
  const navigate = useNavigate();

  //      formData  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value
    }));
  };

  //        
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      //     
      const response = await fetch('https://ec2caps.liroocapstone.shop:4000/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });

      //    ,  ID      
      if (response.ok) {
        const data = await response.json();
        sessionStorage.setItem('userId', data.id);
        setLoginSuccess(true);
        navigate('/Main/*');
      } else {
        //    ,    false 
        console.error(' :', response.status);
        setLoginSuccess(false);
      }
    } catch (error) {
      //              
      console.error(' :', error);
      setLoginSuccess(false);
    }
  };

  //       
  const handleNaverLogin = () => {
    window.location.href = 'NaverLogin';
  };

  const handleKakaoLogin = () => {
    window.location.href = 'https://kauth.kakao.com/oauth/authorize?client_id=0bee6abe1a644025c9faedffda0ddd04&redirect_uri=https%3A%2F%2FSEUNGH00N.github.io%2FMain&response_type=code&ka=sdk%2F1.43.2%20os%2Fjavascript%20sdk_type%2Fjavascript%20lang%2Fko-KR%20device%2FWin32%20origin%2Fhttps%253A%252F%252FSEUNGH00N.github.io&origin=https%3A%2F%2FSEUNGH00N.github.io';
  };


  //      
  const handleSignup = () => {
    navigate('/Signup');
  };

  //       
  const handleFindId = () => {
    navigate('/IdFind');
  };

  //       
  const handleFindPassword = () => {
    navigate('/PwFind');
  };

  //    JSX
  return (
    <div className="login-container">
      <img src={logo} id='login-logo' alt="" />
      <h1 className="login-header">L O G I N</h1>
      <form onSubmit={handleSubmit}>

        {/*  ID   */}
        <div className="form-group">
          <input
            type="text"
            name="id"
            value={formData.id}
            onChange={handleChange}
            placeholder=""
            required
          />
        </div>

        {/*    */}
        <div className="form-group">
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            placeholder=""
            required
          />
        </div>

        {/*    */}
        {!loginSuccess && (
          <p className="login-failure-message">    .</p>
        )}


        {/*   */}
        <button type="submit" className="login-button"></button>

        {/* //   */}
        <div className="all-group">
          <button type="button" className="Signup-button" onClick={handleSignup}></button>
          <button type="button" className="find-id" onClick={handleFindId}> </button>
          <button type="button" className="find-pw" onClick={handleFindPassword}> </button>
        </div>

        {/* Rest   */}
        <div className="rest-group">
          <button type="button" src={naver} alt="naver" className="naver-login" onClick={handleNaverLogin}></button>
          <button type="button" src={kakao} alt="kakao" className="kakao-login" onClick={handleKakaoLogin}></button>
        </div>

      </form>
    </div>
  );
}

export default Login;