import React, { useState } from 'react';
import '../../styles/main.css';
import logo from '../../image/logo.png';

function PwFind() {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [showModal, setShowModal] = useState(false);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handleResetPassword = async () => {
    try {
      const response = await fetch('https://ec2caps.liroocapstone.shop:4000/reset-password', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ email })
      });

      if (response.ok) {
        const data = await response.json();
        setMessage(data.message);
      } else {
        setMessage('  .');
      }
    } catch (error) {
      console.error('  :', error);
      setMessage('      .');
    } finally {
      setShowModal(true);
    }
  };

  const closeModal = () => {
    setShowModal(false);
  };

  const navigateToLogin = () => {
    window.location.href = '/Login';
  };

  return (
    <div className="pwfind-container">
      <img src={logo} id='pwfind-logo' alt="" />
      <h1 className="pwfind-header"> </h1>
      <form onSubmit={(e) => { e.preventDefault(); handleResetPassword(); }}>
        <input
          type="email"
          value={email}
          onChange={handleEmailChange}
          placeholder=""
          required
        />
        <button type="submit" className="pwfind"> </button>
      </form>

      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <p className="found-pw">{message}</p>
            <div className="pwfind-modal">
              {message === '  .' ? (
                <button className="modal-close" onClick={closeModal}></button>
              ) : (
                <button className="found-id" onClick={navigateToLogin}>  </button>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default PwFind;