import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../styles/main.css';

function AddProducts() {
  const userId = sessionStorage.getItem('userId');
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [image, setImage] = useState(null); //  
  const navigate = useNavigate(); 

  const handleAddProduct = async (event) => {
    event.preventDefault(); //     

    //   
    try {
      const formData = new FormData();
      formData.append('userId', userId);
      formData.append('name', name);
      formData.append('description', description);
      formData.append('price', price);
      formData.append('image', image); //  

      const response = await fetch('https://ec2caps.liroocapstone.shop:4000/addProduct', {
        method: 'POST',
        headers: {
          'user_id': userId,
        },
        body: formData, //   FormData 
      });

      if (response.ok) {
        //      
        setName('');
        setDescription('');
        setPrice('');
        setImage(null); //  
        //      
        navigate('/Main/*');
        console.log(' .');
      } else {
        //       
        console.error('  :', response.statusText);
      }
    } catch (error) {
      //         
      console.error('  :', error);
    }
  };

  return (
    <div className="add-products-container">
      <h2> </h2>
      <form onSubmit={handleAddProduct}>
        <div className="form-group">
          <label htmlFor="name">:</label>
          <input type="text" id="name" value={name} onChange={(e) => setName(e.target.value)} required />
        </div>
        <div className="form-group">
          <label htmlFor="description">:</label>
          <input type="text" id="description" value={description} onChange={(e) => setDescription(e.target.value)} required />
        </div>
        <div className="form-group">
          <label htmlFor="price">:</label>
          <input type="text" id="price" value={price} onChange={(e) => setPrice(e.target.value)} required />
        </div>
        <div className="form-group">
          <label htmlFor="image">:</label> {/*   */}
          <input type="file" id="image" onChange={(e) => setImage(e.target.files[0])} required />
        </div>
        <button type="submit" className="add-product-button"></button>
      </form>
    </div>
  );
}

export default AddProducts;
