import React, { useState } from 'react';
import '../../styles/main.css';
import logo from '../../image/logo.png';

function IdFind() {
  const [formData, setFormData] = useState({
    email: '',
    department: '',
    grade: ''
  });
  const [id, setId] = useState('');
  const [showNotFoundModal, setShowNotFoundModal] = useState(false);
  const [showFoundModal, setShowFoundModal] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value
    }));
  };

  //     
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch('https://ec2caps.liroocapstone.shop:4000/find-id', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });

      if (response.ok) {
        const data = await response.json();
        setId(data.id);
        setShowFoundModal(true);
      } else {
        setShowNotFoundModal(true);
      }
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleCloseNotFoundModal = () => {
    setShowNotFoundModal(false);
  };

  const navigateToLogin = () => {
    window.location.href = '/Login';
  };
  const navigateToPwFind = () => {
    window.location.href = '/PwFind';
  };

  return (
    <div className="id-find-container">
      <img src={logo} id='idfind-logo' alt="" />
      <h1 className="idfind-header"> </h1>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            placeholder=" "
            required
          />
        </div>

        {/* /   */}
        <div className="select-group">
          <select
            name="department"
            value={formData.department}
            onChange={handleChange}
            style={{ color: formData.department ? 'black' : 'gray' }}
            required
          >
            <option value=""> </option>
            <option value="computer_science"> </option>
            <option value="software_engineering"> </option>
            <option value="design"></option>
            <option value="business-administration"></option>
          </select>

          <select
            name="grade"
            value={formData.grade}
            onChange={handleChange}
            style={{ color: formData.grade ? 'black' : 'gray' }}
            required
          >
            <option value=""> </option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>
        <button type="submit" className="idfind"> </button>
      </form>

      {showNotFoundModal && (
        <div className="modal">
          <div className="modal-content">
            <p className="not-found">   .</p>
            <button className="modal-close" onClick={handleCloseNotFoundModal}></button>
          </div>
        </div>
      )}

      {showFoundModal && (
        <div className="modal">
          <div className="modal-content">
            <p className="found-id"> : {id}</p>
            <div className="idfind-modal">
              <button className="idfind-login" onClick={navigateToLogin}> </button>
              <button className="idfind-fwfind" onClick={navigateToPwFind}> </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default IdFind;