
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../../styles/main.css';
import logo from '../../image/logo.png';

function Signup() {
  const [signupSuccess, setSignupSuccess] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [formData, setFormData] = useState({
    id: '',
    name: '',
    password: '',
    confirmPassword: '',
    email: '',
    department: '',
    grade: ''
  });
  const [idAvailability, setIdAvailability] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleCheckAvailability = async () => {
    try {
      const response = await fetch(`https://ec2caps.liroocapstone.shop:4000/checkUser?id=${formData.id}`);
      if (response.ok) {
        const data = await response.json();
        setIdAvailability(data.available);
        setIsModalOpen(true);
        setErrorMessage(data.available ? '  !' : '  !');
      } else {
        setIdAvailability(null);
        setErrorMessage('     .');
        setIsModalOpen(true); // Open modal on error
      }
    } catch (error) {
      console.error('Error:', error);
      setIdAvailability(null);
      setErrorMessage('    .');
      setIsModalOpen(true); // Open modal on error
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (idAvailability === null || idAvailability === false) {
      setErrorMessage('    .');
      setIsModalOpen(true); // Open modal for error
      return;
    }

    try {
      const response = await fetch('https://ec2caps.liroocapstone.shop:4000/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });
      if (response.ok) {
        handleSignupSuccess();
      } else {
        const errorData = await response.json();
        setErrorMessage(errorData.error);
        setIsModalOpen(true); // Open modal on error
      }
    } catch (error) {
      console.error('Error:', error);
      setErrorMessage('    .');
      setIsModalOpen(true); // Open modal on error
    }
  };

  const handleSignupSuccess = () => {
    setSignupSuccess(true);
    setErrorMessage('');
    navigate('/login');
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
    setErrorMessage('');
  };

  return (
    <div className="Signup-container">
      {isModalOpen && (
        <div className="modal">
          <div className="modal-content">
            <p>{errorMessage}</p>
            <button className="modal-close" onClick={handleModalClose}></button>
          </div>
        </div>
      )}
      {signupSuccess && <p className="success-message"> </p>}
      <img src={logo} id='Signup-logo' alt="" />
      <h1 className="Signup-header">   </h1>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder=""
            required
          />
        </div>
        <div className="form-group id-group">
          <div className="id-form">
            <input
              type="text"
              name="id"
              value={formData.id}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>
          <button type="button" className="check" onClick={handleCheckAvailability}> </button>
        </div>
        <div className="form-group">
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            placeholder=""
            required
          />
        </div>
        <div className="form-group">
          <input
            type="password"
            name="confirmPassword"
            value={formData.confirmPassword}
            onChange={handleChange}
            placeholder=" "
            required
          />
        </div>
        <div className="form-group">
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            placeholder=""
            required
          />
        </div>
        <div className="select-group">
          <select
            name="department"
            value={formData.department}
            onChange={handleChange}
            style={{ color: formData.department ? 'black' : 'gray' }}
            required
          >
            <option value=""> </option>
            <option value="computer_science"> </option>
            <option value="software_engineering"> </option>
            <option value="design"></option>
            <option value="business-administration"></option>
          </select>

          <select
            name="grade"
            value={formData.grade}
            onChange={handleChange}
            style={{ color: formData.grade ? 'black' : 'gray' }}
            required
          >
            <option value=""> </option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>
        <button type="submit" className="Signup"></button>
      </form>
      <p>  ?&nbsp;&nbsp;<Link to="/Login"></Link></p>
    </div>
  );
}

export default Signup;
